.header {
  height: 216px;
  position: relative;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.56);
  background-image: url(../images/header-mobile-min.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  @include retina {
    background-image: $bg-header-gradient, url(../images/header-mob@2x-min.png);
  }

  @include tablet {
    background-image: $bg-header-gradient, url(../images/header-tablet-min.png);

    @include retina {
      background-image: $bg-header-gradient,
        url(../images/header-tablet@2x-min.png);
    }
  }

  @include desktop {
    background-image: $bg-header-gradient, url(../images/header-desktop-min.png);

    @include retina {
      background-image: $bg-header-gradient,
        url(../images/header-desktop@2x-min.png);
    }
  }
}

.header-wraper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 54px;
}

.logo-site:hover > svg,
.logo-site:focus > svg,
.logo-site:hover > span,
.logo-site:focus > span {
  color: $accent-color;
  stroke: $accent-color;
}

.logo {
  stroke: #ffffff;
}

// .logo:hover,
// .logo:focus {
//   stroke: $accent-color;
// }

.logo__name {
  display: none;
}

.logo-container > a {
  display: flex;
  align-items: center;
}

.navigation-list {
  display: flex;
}

.navigation-list__item {
  font-weight: 500;
  line-height: 1.17px;

  &:not(:last-child) {
    margin-right: 32px;
  }
}

.wrapper-switch {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 92px;
}

.header-search-form {
  display: flex;
  justify-content: center;
  padding-bottom: 20px;
  // height: 24px;

  // @include mobile {
  //   width: 280px;
  // }

  // @include desktop {
  //   width: 472px;
  // }
}

.form-search {
  position: relative;
  // padding-bottom: 92px
  // display: inline-block;
}

.form-input {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  color: $primary-text-color;
  width: 280px;
  position: relative;
  border-bottom: 0.5px solid $primary-text-color;

  border-top: none;
  border-left: none;
  border-right: none;
  outline: 0;
  background: transparent;

  @include tablet {
    width: 336px;
  }

  @include desktop {
    width: 394px;
  }
}

.form-input::placeholder {
  color: #ffffff;
}

.search-btn {
  stroke: #ffffff;

  fill: #ffffff;
  position: absolute;
  width: 22px;
  height: 22px;
  top: -4px;
  right: 0;
  border: none;
  border-radius: 50%;
  background: url(../images/search\ 2.svg);
  background-repeat: no-repeat;
  background-position: center;
  transform: scale(1);
  @include transition(transform);

  &:hover,
  &:focus {
    transform: scale(1.2);
  }
}

.site-nav__link--current {
  position: relative;
  display: block;

  &::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 0;
    transform: translateY(1px);
    display: block;
    width: 100%;
    height: 3px;
    border: none;
    background-color: $warning-text-color;

    transition: width 0.2s ease-in-out;
  }

  &:hover::after,
  &:focus::after {
    width: 0;
  }
}

@include tablet {
